import React from 'react';
import {
  Box,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Card,
  CardContent,
  CircularProgress,
} from '@mui/material';
import { Send as SendIcon, Psychology as PsychologyIcon } from '@mui/icons-material';
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { Bike } from '@/types/bike';
import { Category } from '@/types/category';

const schema = yup.object({
  bikeId: yup.number().required(''),
  categoryId: yup.number().required(''),
  question: yup
    .string()
    .required('')
    .max(1000, '1000')
    .min(10, '10'),
});

type FormData = {
  bikeId: number;
  categoryId: number;
  question: string;
};

interface AiQuestionFormProps {
  bikes: Bike[];
  categories: Category[];
  onSubmit: (data: FormData) => void;
  isLoading?: boolean;
  selectedBike?: Bike;
  selectedCategory?: Category;
}

export const AiQuestionForm: React.FC<AiQuestionFormProps> = ({
  bikes,
  categories,
  onSubmit,
  isLoading = false,
  selectedBike,
  selectedCategory,
}) => {
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    watch,
  } = useForm<FormData>({
    resolver: yupResolver(schema),
    defaultValues: {
      bikeId: selectedBike?.id || 0,
      categoryId: selectedCategory?.id || 0,
      question: '',
    },
  });

  const watchedBikeId = watch('bikeId');
  const selectedBikeInfo = bikes.find(bike => bike.id === watchedBikeId);

  const onFormSubmit = (data: FormData) => {
    onSubmit(data);
    reset({
      bikeId: data.bikeId,
      categoryId: data.categoryId,
      question: '',
    });
  };

  return (
    <Card>
      <CardContent>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
          <PsychologyIcon sx={{ mr: 1, color: 'primary.main' }} />
          <Typography variant="h6">AI</Typography>
        </Box>

        <Box component="form" onSubmit={handleSubmit(onFormSubmit)}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
            <Controller
              name="bikeId"
              control={control}
              render={({ field }) => (
                <FormControl fullWidth error={!!errors.bikeId} disabled={isLoading}>
                  <InputLabel></InputLabel>
                  <Select {...field} label="">
                    <MenuItem value={0}></MenuItem>
                    {bikes.map((bike) => (
                      <MenuItem key={bike.id} value={bike.id}>
                        {bike.manufacturer} {bike.modelName}
                        {bike.modelYear && ` (${bike.modelYear})`}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.bikeId && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5 }}>
                      {errors.bikeId.message}
                    </Typography>
                  )}
                </FormControl>
              )}
            />

            <Controller
              name="categoryId"
              control={control}
              render={({ field }) => (
                <FormControl fullWidth error={!!errors.categoryId} disabled={isLoading}>
                  <InputLabel></InputLabel>
                  <Select {...field} label="">
                    <MenuItem value={0}></MenuItem>
                    {categories.map((category) => (
                      <MenuItem key={category.id} value={category.id}>
                        {category.name}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.categoryId && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5 }}>
                      {errors.categoryId.message}
                    </Typography>
                  )}
                </FormControl>
              )}
            />

            <Controller
              name="question"
              control={control}
              render={({ field }) => (
                <TextField
                  {...field}
                  fullWidth
                  multiline
                  rows={4}
                  label=""
                  placeholder=": "
                  error={!!errors.question}
                  helperText={errors.question?.message || `${field.value.length}/1000`}
                  disabled={isLoading}
                />
              )}
            />
            
            <Typography variant="body2" color="error" sx={{ mt: -2 }}>
              AI
              
            </Typography>

            <Button
              type="submit"
              variant="contained"
              size="large"
              startIcon={
                isLoading ? <CircularProgress size={20} color="inherit" /> : <SendIcon />
              }
              disabled={isLoading}
              sx={{ alignSelf: 'flex-start' }}
            >
              {isLoading ? '' : 'AI'}
            </Button>
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
};