import axios, { AxiosInstance, AxiosResponse } from 'axios';
import toast from 'react-hot-toast';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8080/api';

/**
 * API
 * 
 */
class ApiClient {
  private client: AxiosInstance;

  /**
   * ApiClient
   */
  constructor() {
    this.client = axios.create({
      baseURL: API_BASE_URL,
      timeout: 60000,
      headers: {
        'Content-Type': 'application/json',
      },
    });

    this.setupInterceptors();
  }

  /**
   * Axios
   * 
   */
  private setupInterceptors() {
    // : 
    this.client.interceptors.request.use(
      (config) => {
        const token = localStorage.getItem('auth_token');
        if (token) {
          config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
      },
      (error) => Promise.reject(error)
    );

    // : 
    this.client.interceptors.response.use(
      (response: AxiosResponse) => response,
      (error) => {
        const { status } = error.response || {};

        switch (status) {
          case 401:
            localStorage.removeItem('auth_token');
            localStorage.removeItem('user_data');
            window.location.href = '/login';
            toast.error('');
            break;
          case 403:
            toast.error('');
            break;
          case 404:
            toast.error('');
            break;
          case 500:
            toast.error('');
            break;
          default:
            const errorMessage = error.response?.data?.message || '';
            toast.error(errorMessage);
        }

        return Promise.reject(error);
      }
    );
  }

  /**
   * HTTP GET
   * @template T 
   * @param url URL
   * @returns Promise
   */
  public get<T>(url: string): Promise<T> {
    return this.client.get(url).then((response) => response.data);
  }

  /**
   * HTTP POST
   * @template T 
   * @param url URL
   * @param data  ()
   * @returns Promise
   */
  public post<T>(url: string, data?: any): Promise<T> {
    return this.client.post(url, data).then((response) => response.data);
  }

  /**
   * HTTP PUT
   * @template T 
   * @param url URL
   * @param data  ()
   * @returns Promise
   */
  public put<T>(url: string, data?: any): Promise<T> {
    return this.client.put(url, data).then((response) => response.data);
  }

  /**
   * HTTP PATCH
   * @template T 
   * @param url URL
   * @param data  ()
   * @returns Promise
   */
  public patch<T>(url: string, data?: any): Promise<T> {
    return this.client.patch(url, data).then((response) => response.data);
  }

  /**
   * HTTP DELETE
   * @template T 
   * @param url URL
   * @returns Promise
   */
  public delete<T>(url: string): Promise<T> {
    return this.client.delete(url).then((response) => response.data);
  }
}

export const apiClient = new ApiClient();