import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import { authService } from '@/services/authService';
import { useAuthStore } from '@/stores/authStore';
import { UserUpdateRequest } from '@/types/user';

export const useAuth = () => {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const { login, logout, updateUser, setLoading, setError } = useAuthStore();

  const loginMutation = useMutation({
    mutationFn: authService.loginWithGoogle,
    onMutate: () => {
      setLoading(true); 
      setError(null);
    },
    onSuccess: (data) => {
      login(data.accessToken, data.user);
      toast.success('');
      navigate('/dashboard');
    },
    onError: (error: any) => {
      const message = error?.response?.data?.message || '';
      setError(message);
      toast.error(message);
    },
    onSettled: () => {
      setLoading(false); 
    },
  });

  const guestLoginMutation = useMutation({
    mutationFn: authService.loginAsGuest,
    onMutate: () => {
      setLoading(true); 
      setError(null);
    },
    onSuccess: (data) => {
      login(data.accessToken, data.user);
      localStorage.setItem('is_guest', 'true');
      toast.success('');
      navigate('/dashboard');
    },
    onError: (error: any) => {
      const message = error?.response?.data?.message || '';
      setError(message);
      toast.error(message);
    },
    onSettled: () => {
      setLoading(false);
    },
  });

  const updateUserMutation = useMutation({
    mutationFn: ({ userId, updateData }: { userId: number; updateData: UserUpdateRequest }) =>
      authService.updateUser(userId, updateData),
    onSuccess: (updatedUser) => {
      updateUser(updatedUser);
      queryClient.invalidateQueries({ queryKey: ['user'] });
      toast.success('');
    },
    onError: (error: any) => {
      const message = error?.response?.data?.message || '';
      toast.error(message);
    },
  });

  const deleteUserMutation = useMutation({
    mutationFn: authService.deleteUser,
    onSuccess: () => {
      logout();
      queryClient.clear();
      toast.success('');
      navigate('/');
    },
    onError: (error: any) => {
      const message = error?.response?.data?.message || '';
      toast.error(message);
    },
  });

  const handleLogout = () => {
    logout();
    localStorage.removeItem('is_guest');
    queryClient.clear();
    toast.success('');
    navigate('/');
  };

  return {
    loginMutation,
    guestLoginMutation,
    updateUserMutation,
    deleteUserMutation,
    handleLogout,
  };
};